body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: rgb(231, 222, 222);
}

.newsContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.box {
  width: 40%;
  height: 200px;
  border: 1px solid black;
  border-radius: 10px;
  margin-bottom: 10px;
  margin-left: 10px;
  display: flex;
  flex-direction: row;
}

.thumbnail {
  width:40%;
  border-radius: 10px;
  margin:10px
}

.content {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
}

.footer {
  font-size: 12px;
  margin-top: auto;
  display:flex;
  justify-content: space-between;
}

.categories{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.category-button
{
  border: 1px solid black;
  border-radius: 50px;
  margin: 10px;
  padding: 10px;
}

.category-button-clicked{
  background-color: #f8b4b4;
}

.category-button:hover{
  background-color: #f8b4b4;
  cursor: pointer ;
}
